<template>
    <div class="dashboard">
        <h1 class="subtitle-1 grey--text">dashboard</h1>

        <v-container class="my-5">

            <v-layout row class="mb-3">
                <v-tooltip top>
                    <template v-slot:activator="{ on }">
                        <v-btn small text color="grey mr-2" @click="sortBy('title')" v-on="on">
                        <v-icon left small color="grey">mdi-folder</v-icon>
                        <span right class="caption text-lowercase">by project name</span>
                        </v-btn>
                    </template>
                    <span>Sorts projects by name</span>
                </v-tooltip>
                <v-tooltip top>
                    <template v-slot:activator="{ on }">
                        <v-btn small text color="grey mr-2" @click="sortBy('person')" v-on="on">
                        <v-icon left small color="grey">mdi-account</v-icon>
                        <span right class="caption text-lowercase">by person</span>
                        </v-btn>
                    </template>
                    <span>Sorts projects by Person</span>
                </v-tooltip>
            </v-layout>

            <v-card flat class="" v-for="project in projects" :key="project.title">
                <v-layout row :class="`pa-3 project ${project.status}`">
                    <v-flex xs12 md6>
                        <div class="caption grey--text">Project title</div>
                        <div>{{ project.title }}</div>
                    </v-flex>
                    <v-flex xs6 sm4 md2>
                        <div class="caption grey--text">Person</div>
                        <div>{{ project.person }}</div>
                    </v-flex>
                    <v-flex xs6 sm4 md2>
                        <div class="caption grey--text">Due By</div>
                        <div>{{ project.due }}</div>
                    </v-flex>
                    <v-flex xs6 sm4 md2 pr-12>
                        <div class="text-right">
                            <v-chip small :class="`v-chip--active ${project.status} white--text caption my-2`">{{ project.status }}</v-chip>
                        </div>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
            </v-card>
        </v-container>
        
    </div>
</template>

<script>
import db from '@/fb'

export default {
    data() {
        return {
            projects: []
        }
    },
    methods: {
        sortBy(prop) {
            this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
        }
    },
    created() {
        db.collection('projects').onSnapshot(res => {
            const changes = res.docChanges();
            changes.forEach(change => {
                if (change.type === 'added') {
                    this.projects.push({
                        ...change.doc.data(),
                        id: change.doc.id
                    })
                } else if (change.type === 'removed') {
                    this.projects = this.projects.filter(function(n) { return n.id !== change.doc.id; });
                }
            })
        })
    }
}
</script>

<style>
.project.complete {
    border-left: 4px solid #3cd1c2;
}
.project.ongoing {
    border-left: 4px solid orange;
}
.project.overdue {
    border-left: 4px solid tomato;
}
.v-chip.complete {
    background: #3cd1c2;
}
.v-chip.ongoing {
    background: #ffaa2c;
}
.v-chip.overdue {
    background: #f83e70;
}
</style>